<template>
  <div class="hello">
    {{boardList}}
    <router-link to="/community">community</router-link>
  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      listData:[]
    }
  },
  computed: {
    boardList () {
      db.collection('user').get().then( (querySnapshot) => {
        querySnapshot.forEach((field) => {
          console.log(field.data().name)
          this.listData.push(field.data())
        })
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



