<template>
  <div class="community">
    {{boardList}}
    <!--table area-->
      <div class="tbl_content">
      <table cellpadding="0" cellspacing="0" border="0">
       <tbody>
         <tr>
           <td class="title">이름</td>
           <td><input type="text" v-model="addList.name"></td>
           <td class="title">나이</td>
           <td><input type="text" v-model="addList.age"></td>
         </tr>
       </tbody>
      </table>
      <div class="btn_group right">
        <router-link to="/community" class="gbtn">list</router-link>
        <a href="#" v-on:click.prevent="submitList" class="gbtn">submit</a>
      </div>
      <!--table area-->
      <div class="tbl_header">
        <table cellpadding="0" cellspacing="0" border="0">
          <thead>
            <tr>
              <th class="no">번호</th>
              <th>이름</th>
              <th>나이</th>
              <th>날짜</th>
            </tr>
          </thead>
        </table>
      </div>
      <div class="tbl_content">
        <table cellpadding="0" cellspacing="0" border="0">
         <tbody>
           <tr v-for="(list, index) in listData">
             <td class="no">{{listData.length - index}}</td>
             <td>{{list.name}}</td>
             <td>{{list.age}}</td>
             <td>{{list.date}}</td>
           </tr>
         </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'SubmitComponent',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      listData:[],
      addList: {
        name: '',
        age: '',
        date: null,
        index: 0
      }
    }
  },
  computed: {
    boardList () {
      db.collection('user').orderBy("index", "desc").get().then( (querySnapshot) => {
        this.listData = []
        querySnapshot.forEach((field) => {
          console.log(field.data().name)
          this.listData.push(field.data())
        })
      })
    }
  },
  methods: {
    submitList () {
      if( this.addList.name == '' || this.addList.age == '' ){
        alert('입력 값이 비어있습니다. 이름과 나이를 입력해주세요.')
      }else if( isNaN(this.addList.age) ){
        alert('나이는 숫자로 입력해주세요')
      }else{
        this.addList.date = new Date().toJSON().slice(0,10).replace(/-/g,'.')//날짜
        this.addList.index = Number(new Date().toJSON().slice(0,20).replace(/-/g,'').replace(/T/g,'').replace(/:/g,''))//날짜로 인덱스 정렬
        db.collection('user').add(this.addList).then( (querySnapshot) => {
          alert(this.addList.name + '님이 가입하였습니다.')
          this.addList.name = ''
          this.addList.age = ''
          db.collection('user').orderBy("index", "desc").get().then( (querySnapshot) => {
            this.listData = []
            querySnapshot.forEach((field) => {
              console.log(field.data().name)
              this.listData.push(field.data())
            })
          })
        })
      }
    }
  }
}
</script>
<style lang="scss">
</style>



