<template>
  <div class="community">
    {{boardList}}
    <!--table area-->
    <div class="tbl_header">
      <table cellpadding="0" cellspacing="0" border="0">
        <thead>
          <tr>
            <th class="no">번호</th>
            <th>이름</th>
            <th>나이</th>
            <th>날짜</th>
            <th class="delete">삭제</th>
          </tr>
        </thead>
      </table>
    </div>
    <div class="tbl_content">
      <table cellpadding="0" cellspacing="0" border="0">
       <tbody>
         <tr v-for="(list, index) in listData">
           <td class="no">{{listData.length - index}}</td>
           <td>{{list.data().name}}</td>
           <td>{{list.data().age}}</td>
           <td>{{list.data().date}}</td>
           <td class="delete"><button class="gbtn delete" v-on:click="deleteList(index,list.id)">delete</button></td>
         </tr>
       </tbody>
      </table>
      <router-link to="/submitpage" class="gbtn">write</router-link>
    </div>
  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'Community',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      listData:[],
      addList: {
        name: '',
        age: '',
        date: null
      }
    }
  },
  computed: {
    boardList () {
      db.collection('user').orderBy("index", "desc").get().then( (querySnapshot) => {
        this.listData = []
        querySnapshot.forEach((field) => {
          this.listData.push(field)
        })
      })
      .catch(function(error) {
          // The document probably doesn't exist.
          console.error("Error updating document: ", error);
      })
    }
  },
  methods: {
    deleteList (index, id) {
      console.log(id)
      db.collection('user').doc(id).delete().then((querySnapshot) => {
        db.collection('user').orderBy("index", "desc").get().then( (querySnapshot) => {
          this.listData = []
          querySnapshot.forEach((field) => {
            this.listData.push(field)
          })
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        })
      })
      .catch(function(error) {
          // The document probably doesn't exist.
          console.error("Error updating document: ", error);
      })
    }
  }
}
</script>
<style lang="scss">
</style>



